interface IReAppealData {
  stageName: string;
  assignTo: {
    name: string;
    uid: string;
  };
  reasonForReappeal: string;
  reappealDocs: IDocumentInfo;
}

interface IRejectDropdown {
  code: string;
  name: string;
}

const STAGE_AND_STATUS_MENU = [
  {
    id: 1,
    name: "Stages",
    path: LINK_DDE_STAGE_AND_STATUS_STAGES,
  },
  {
    id: 2,
    name: "Stage History",
    path: LINK_DDE_STAGE_AND_STATUS_HISTORY,
  },
];

const initialReappealData = {
  stageName: "",
  assignTo: {
    name: "",
    uid: "",
  },
  reasonForReappeal: "",
  reappealDocs: initialDocInfo,
};

function ScrollableTabs() {
  const params = useParams();
  const scrollContainerRef = useRef<HTMLDivElement | null>(null); // Explicitly set the type
  const [showLeftArrow, setShowLeftArrow] = useState(false);
  const [showRightArrow, setShowRightArrow] = useState(false);

  useEffect(() => {
    const scrollContainer = scrollContainerRef.current;

    if (scrollContainer) {
      // Check if there's content to scroll to the left
      setShowLeftArrow(scrollContainer.scrollLeft > 0);

      // Check if there's content to scroll to the right
      setShowRightArrow(
        scrollContainer.scrollLeft + scrollContainer.clientWidth < scrollContainer.scrollWidth
      );

      // Listen for scroll events
      const handleScroll = () => {
        setShowLeftArrow(scrollContainer.scrollLeft > 0);
        setShowRightArrow(
          scrollContainer.scrollLeft + scrollContainer.clientWidth < scrollContainer.scrollWidth
        );
      };

      // Add a scroll event listener for the scrollContainer
      scrollContainer.addEventListener("scroll", handleScroll);

      // Add a wheel event listener for mouse scroll
      const handleWheel = (event: WheelEvent) => {
        // You can adjust the scrolling speed by changing the value (-50 in this case)
        scrollContainer.scrollLeft += event.deltaX;
      };

      scrollContainer.addEventListener("wheel", handleWheel);

      // Cleanup the event listeners
      return () => {
        scrollContainer.removeEventListener("scroll", handleScroll);
        scrollContainer.removeEventListener("wheel", handleWheel); // Remove the wheel event listener
      };
    }
  }, []);

  const handleScroll = (scrollOffset: number) => {
    // Specify the type as number
    const scrollContainer = scrollContainerRef.current;

    if (scrollContainer) {
      scrollContainer.scrollBy({
        left: scrollOffset,
        behavior: "smooth",
      });
    }
  };
  const getClassMenu = ({ isActive }: any) => {
    const className = `tab text-sm px-2.5 py-1 min-w-fit ${
      isActive
        ? "bg-secondary-steelBlue-900 rounded-sm text-white font-medium shadow-activeBtn "
        : "text-primary-500"
    }`;
    return className;
  };
  return (
    <div className="scroll-container">
      <div ref={scrollContainerRef} className="content">
        {STAGE_AND_STATUS_MENU.map((menu) => (
          <NavLink
            key={menu.path}
            className={getClassMenu}
            to={`${linkAuthRoute}${LINK_LOAN_DETAIL}${LINK_DDE_STAGE_AND_STATUS}/${params.loanUid}${menu.path}`}
          >
            {menu.name}
          </NavLink>
        ))}
      </div>
      {showLeftArrow && (
        <button
          title="Scroll left"
          type="button"
          className="p-panelmenu-icon pi pi-chevron-left scroll-button left"
          onClick={() => handleScroll(-200)} // Adjust the scrolling amount
        ></button>
      )}
      {showRightArrow && (
        <button
          title="Scroll right"
          type="button"
          className="p-panelmenu-icon pi pi-chevron-right scroll-button right"
          onClick={() => handleScroll(200)} // Adjust the scrolling amount
        ></button>
      )}
    </div>
  );
}

interface RejectLoanAppType {
  isRejectAppConfirmationVisible: boolean;
  reasonToReject: string;
  description: string;
}

const StageAndStatus = (props: RouteProps) => {
  const params = useParams();
  const loanId = params.loanUid ?? "";
  const dispatch = useDispatch();
  const [applicantId, setApplicantId] = useState("");

  // ----------------- states ---------------------- //
  // main page functionality states

  const [isLoanRejected, setIsLoanRejected] = useState(false);

  const [rejectLoanAppDetails, setRejectLoanAppDetails] = useState<RejectLoanAppType>({
    isRejectAppConfirmationVisible: false,
    reasonToReject: "",
    description: "",
  });
  const [isReAppealDialogVisible, setIsReAppealDialogVisible] = useState<boolean>(false);
  const [reAppealData, setReAppealData] = useState<IReAppealData>(initialReappealData);
  const [isAssigneeSuggestionLoading, setIsAssigneeSuggestionLoading] = useState(false);
  const [rejectionReasonOptions, setRejectionReasonOptions] = useState<IRejectDropdown[]>([]);
  const [suggestionsTemplate, setSuggestionsTemplate] = useState<
    {
      name: string | undefined;
      code: string | undefined;
    }[]
  >([]);

  //error handling states
  const [position, setPosition] = useState<ToastPositionType | undefined>("bottom-right");
  const [showDialogAPIError, setShowDialogAPIError] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");
  const [toastMessage, setToastMessage] = useState("");
  const [toastMessageVariant, setToastMessageVariant] = useState(TOAST_STYLE_TYPE.PRIMARY);
  const [triggerToast, setTriggerToast] = useState(false);

  // --------------------- ref ---------------------------- //
  const toastMessageRef = useRef(null);
  const reAppealUploaderRef = useRef<IDocumentUploaderRef | null>(null);

  // --------------------------- validator initialization ---------------------- //
  const [, forceUpdate] = useState<boolean>(false); //NOSONAR

  const simpleValidator = useRef(
    new SimpleReactValidator({
      validators: { ...getStandardValidator() },
      ...messageReplace(),
      autoForceUpdate: { forceUpdate: () => forceUpdate((prev) => !prev) },
    })
  );

  /*
    --------------------------------------------------------------------------------------------------
    LifeCycle Methods: 
    --------------------------------------------------------------------------------------------------
  */

  useEffect(() => {
    getLoanDetail();
    getRejectionOptions();
    getApplicantId();
  }, []);

  useEffect(() => {
    if (errorMessage) {
      setShowDialogAPIError(true);
    }
  }, [errorMessage]);

  useEffect(() => {
    if (toastMessageRef?.current && toastMessage !== "") {
      // @ts-ignore: will be fixed in future
      toastMessageRef?.current?.showFunction();
    }
  }, [toastMessage, toastMessageVariant, triggerToast]);

  /*
    -------------------------------------------------------------------------------------------------------
    Service Calls: For Getting Data from service/API
    -------------------------------------------------------------------------------------------------------
  */

  const getLoanDetail = () => {
    const uid = params.loanUid;
    const search = `&uid=${uid}`;
    LoanService.getAll(0, 1, search)
      .then((res) => {
        const results = res.data.results[0];
        if (results.loanStatus === LOAN_STATUS_ITEM.REJECTED) {
          setIsLoanRejected(true);
        }
        dispatch(updateLoanDetail(results));
      })
      .catch((err) => {
        setErrorMessage(parseStandardAPIErrorMessage(err));
      });
  };

  const getRejectionOptions = async () => {
    StaticMasterDataService.getAll(
      0,
      1000,
      "staticMasterType.code=LOAN_REJECTION_REASON&"
    )
      .then((res) => {
        if (validateGetAPIResponse(res)) {
          const options = res?.data?.results?.map((record: any) => {
            return {
              name: record?.name ?? "",
              code: record?.code ?? "",
            };
          });
          setRejectionReasonOptions(options?.length ? options : []);
        }
      })
      .catch((err) => {
        console.log(err);
        setErrorMessage(parseStandardAPIErrorMessage(err));
      });
  };

  const getApplicantId = async () => {
    try {
      const res = await ApplicantService.getApplicantInfo(0, 20, APPLICANT_TYPE.PRIMARY, loanId);
      if (validateGetAPIResponse(res) && !!res.data?.totalRecords) {
        setReAppealData((prev) => ({
          ...prev,
          applicantUid: res.data?.results?.[0]?.uid,
        }));
        setApplicantId(res.data?.results?.[0]?.uid);
        return res.data?.results?.[0]?.uid ?? "";
      }
    } catch (err) {
      setErrorMessage(parseStandardAPIErrorMessage(err));
    }
  };

  const rejectLoanApplication = async () => {
    if (!simpleValidator?.current?.fields?.reasonToReject) {
      simpleValidator?.current?.showMessages();
      return;
    }

    const payload = {
      rejectRemark: rejectLoanAppDetails?.description,
      reasonToReject: rejectLoanAppDetails?.reasonToReject,
    };

    try {
      const res = await StageService.rejectLoan(loanId, payload);
      if (validatePutAPIResponse(res)) {
        setRejectLoanAppDetails((prevState: RejectLoanAppType) => {
          return {
            ...prevState,
            isRejectAppConfirmationVisible: false,
          };
        });
        setIsLoanRejected(true);
      }
    } catch (error) {
      setRejectLoanAppDetails((prevState: RejectLoanAppType) => {
        return {
          ...prevState,
          isRejectAppConfirmationVisible: false,
          reasonToReject: "",
          description: "",
        };
      });
      setErrorMessage(parseStandardAPIErrorMessage(error));
    }
  };

  /* 
    ---------------------------------------------------------------------------------------------------
    Validators:
    ---------------------------------------------------------------------------------------------------
  */

  const validate = (e: any) => {
    simpleValidator?.current?.showMessageFor(e?.target?.name);
  };

  const validateForm = () => {
    let isValid = false;

    isValid = simpleValidator?.current?.allValid();
    if (isValid) {
      return isValid;
    } else {
      forceUpdate((prev) => !prev);
      simpleValidator?.current?.showMessages();
      forceUpdate((prev) => !prev);
      return false;
    }
  };

  /*
    -----------------------------------------------------------------------------------------------
    Handlers: Event handler for onChange, onBlur etc
    -----------------------------------------------------------------------------------------------
  */

  const handleReAppealDataChange = (e: any) => {
    setReAppealData((prevState: IReAppealData) => ({
      ...prevState,
      [e.target.name]: e.target.value,
    }));
  };

  const handleRejectLoanChange = (e: any) => {
    setRejectLoanAppDetails((prevState: RejectLoanAppType) => {
      return {
        ...prevState,
        [e.target.name]: e.target.value,
      };
    });
  };

  const handleAutoCompleteChange = (event: any) => {
    const { name, value } = event.target;

    if (typeof value === "object") {
      setReAppealData((prevState: IReAppealData) => ({
        ...prevState,
        [name]: { name: value?.name, uid: value?.uid },
      }));
    } else {
      setReAppealData((prevState: IReAppealData) => ({
        ...prevState,
        [name]: { name: value, uid: "" },
      }));
    }
  };

  /*
    -----------------------------------------------------------------------------------------------
    on-Handlers: Event handler for onClick, onToggle etc
    -----------------------------------------------------------------------------------------------
  */

  const onReAppealApplication = async () => {
    setIsReAppealDialogVisible(true);
  };

  const onRejectApplication = () => {
    setRejectLoanAppDetails((prevState: RejectLoanAppType) => {
      return {
        ...prevState,
        isRejectAppConfirmationVisible: true,
      };
    });
  };

  const onReAppeal = async () => {
    const uploadedReAppealDocs = reAppealUploaderRef?.current?.getUploadedData();

    if (!validateForm() || !uploadedReAppealDocs?.documentUid) {
      return;
    }
    const payload = enrichReAppeal(reAppealData, uploadedReAppealDocs);

    try {
      const res = await StageService.createReAppeal(payload);

      if (validatePostAPIResponse(res)) {
        setIsReAppealDialogVisible(false);

        setToastMessageVariant(TOAST_STYLE_TYPE.PRIMARY);
        setToastMessage(STATIC_MASTER_TYPE_TOAST_TEXT.SAVED);
        setTriggerToast(!triggerToast);
        resetValidator();
      }
    } catch (error) {
      console.log(error);
      setErrorMessage(parseStandardAPIErrorMessage(error));
      setIsReAppealDialogVisible(false);
      resetValidator();
    }
  };

  const onCancelReAppeal = () => {
    resetValidator();
    setReAppealData(initialReappealData);
    setIsReAppealDialogVisible(false);
    reAppealUploaderRef?.current?.resetDocUpload();
  };

  /* 
    -------------------------------------------------------------------------------------------------
    Helper Functions:
    -------------------------------------------------------------------------------------------------
  */

  const searchAssignee = async (event: any) => {
    const { originalEvent } = event;

    let suggestionsTemplate: any[] = [];
    if (originalEvent?.target?.name === "assignee") setIsAssigneeSuggestionLoading(true);

    try {
      await StageService.findStageAssignee(0, 10, event.query, loanId, STAGE_TYPE.REAPPEAL).then(
        (res) => {
          if (validateGetAPIResponse(res)) {
            suggestionsTemplate = [...res.data];
            if (originalEvent?.target?.name === "assignee") setIsAssigneeSuggestionLoading(false);
          }
        }
      );
    } catch (error) {
      console.log(error);
      setErrorMessage(parseStandardAPIErrorMessage(error));
    }

    if (suggestionsTemplate.length === 0) {
      suggestionsTemplate.push({ name: undefined, code: undefined, uid: undefined });
    }

    setSuggestionsTemplate(suggestionsTemplate);
  };

  const resetValidator = () => {
    simpleValidator.current = new SimpleReactValidator({
      validators: { ...getStandardValidator() },
      ...messageReplace(),
      autoForceUpdate: { forceUpdate: () => forceUpdate((prev) => !prev) },
    });
    simpleValidator?.current?.hideMessages();
  };

  const enrichReAppeal = (reAppealData: IReAppealData, uploadedDocs?: IDocumentInfo) => {
    const prepareDocSections = (documentSections: IUploadedDocumentSection[]) => {
      const sections = documentSections.map((docSection: any) => ({
        uid: docSection.uid ? docSection.uid : null,
        section: docSection.code,
        fileName: docSection.fileName,
        fileType: docSection.contentType,
        thumbUrl: docSection.thumbUrl,
        url: docSection.url,
        isActive: docSection.isActive,
      }));
      return sections;
    };

    const payloadReAppeal = {
      loan: {
        uid: loanId,
      },
      applicant: {
        uid: applicantId,
      },
      stageName: STAGE_TYPE.REAPPEAL,
      assignee: {
        uid: reAppealData.assignTo.uid,
      },
      delegate: null,
      partner: null,
      remarks: reAppealData.reasonForReappeal,
      smStageType: null,
      stageInitiationDetail: {
        ccEmail: "",
      },
      loanDocuments: uploadedDocs?.documentUid
        ? [
            {
              docType: {
                uid: uploadedDocs?.documentUid,
              },
              docCategory: uploadedDocs?.documentCategory,
              documentSections: prepareDocSections(uploadedDocs?.documentSectionUrls),
            },
          ]
        : [],
      address: null,
    };
    return payloadReAppeal;
  };

  /* 
    ------------------------------------------------------------------------------------------------
    Sectional Render Functions: For Sub components / section wise renders
    ------------------------------------------------------------------------------------------------
  */

  const assigneeItemTemplate = (item: any) => {
    if (item?.name === undefined) {
      return (
        <div className="flex flex-col items-center justify-center h-39 pointer-events-none cursor-default">
          <Text className="text-primary-labelColor mt-2" label="label.noDataFound" />
        </div>
      );
    }
    return (
      <div className="flex flex-col">
        <Text className="text-primary-labelColor" label={item?.name} />
      </div>
    );
  };

  const renderReAppealDialog = () => {
    const { assignTo, reasonForReappeal } = reAppealData;

    return (
      <DialogBox
        className="!w-137 !p-0 reappeal-dialog"
        visible={isReAppealDialogVisible}
        closable={false}
      >
        <div className="py-3 px-6 flex flex-col gap-3 ">
          <div className="flex items-center justify-between">
            <div className="mb-6 font-bold text-base text-primary-primaryTextColor">
              <Text className="" label={"label.heading.stage.re-appeal"} />
            </div>

            <div className="flex items-center gap-5 text-xs text-primary-ptext-1000">
              <div>
                <ButtonBox
                  className=""
                  buttonType={BUTTON_TYPE.CANCEL_OUTLINE}
                  onClick={onCancelReAppeal}
                />
              </div>
              <div>
                <ButtonBox
                  className=""
                  buttonType={BUTTON_TYPE.RE_APPEAL}
                  onClickWithLoader={onReAppeal}
                />
              </div>
            </div>
          </div>

          <div>
            <div className="w-56">
              <AutoComplete
                variant={INPUT_STYLE_TYPE.PRIMARY}
                label={"label.text.stage.assign.to*"}
                placeholder=""
                name="assignTo"
                value={assignTo?.name}
                suggestions={suggestionsTemplate}
                completeMethod={searchAssignee}
                onChange={handleAutoCompleteChange}
                itemTemplate={assigneeItemTemplate}
                loader={isAssigneeSuggestionLoading}
                validation={simpleValidator?.current?.message(
                  "assignTo",
                  assignTo?.uid,
                  "mandatory"
                )}
                onBlur={validate}
              />
            </div>
          </div>

          <div>
            <InputTextArea
              className="!h-40"
              name="reasonForReappeal"
              label={"label.text.descriptionMandatory"}
              placeholder="Enter Description"
              onBlur={validate}
              validation={simpleValidator?.current?.message(
                `reasonForReappeal`,
                reasonForReappeal,
                "mandatory"
              )}
              value={reasonForReappeal}
              onChange={handleReAppealDataChange}
              autoResize
            />
          </div>

          <div>
          
            <TextData
            label="label.dde.stageAndStatus.pageStages"
            titleClass="text-primary-sideBarBgColor text-sm font-bold"
            
            />
            <DocumentUploader
              ref={reAppealUploaderRef}
              mode={PAGE_MODE.EDIT}
              queryParamForDocList={DOCUMENT_CATEGORY.DOCUMENT_CATEGORY_REAPPEAL_DOCUMENTS}
            />
          </div>
        </div>
      </DialogBox>
    );
  };

  const renderRejectLoanDialog = () => {
    return (
      <DialogBox
        className="!w-202.625 !py-4 !px-0"
        visible={rejectLoanAppDetails?.isRejectAppConfirmationVisible}
        closable={false}
      >
        <div className="flex flex-col justify-start gap-6">
          <div className="flex justify-between items-center px-8 border-b pb-2.5">
            <Text
              className="text-base font-bold text-primary-primaryTextColor"
              label={"label.text.rejectLoanApplication"}
            />

            <div className="flex items-center gap-6">
              <ButtonBox
                buttonType={BUTTON_TYPE.NO}
                onClick={() => {
                  resetValidator();
                  setRejectLoanAppDetails((prevState: RejectLoanAppType) => {
                    return {
                      ...prevState,
                      isRejectAppConfirmationVisible: false,
                      reasonToReject: "",
                      description: "",
                    };
                  });
                }}
              />

              <ButtonBox buttonType={BUTTON_TYPE.YES} onClickWithLoader={rejectLoanApplication} />
            </div>
          </div>

          <div className="px-8 flex flex-col justify-start gap-2">
            <div className="w-[62.56%]">
              <SimpleDropDown
                label={"label.text.reasonToReject*"}
                name="reasonToReject"
                value={rejectLoanAppDetails?.reasonToReject}
                onChange={handleRejectLoanChange}
                onBlur={validate}
                options={rejectionReasonOptions}
                optionLabel="name"
                optionValue="code"
                validation={simpleValidator?.current?.message(
                  `reasonToReject`,
                  rejectLoanAppDetails?.reasonToReject,
                  "mandatory"
                )}
              />
            </div>
            <div className="w-[62.56%]">
              <InputTextArea
                hideMaxCount
                disableMaxCount
                name="description"
                className="!h-40"
                label={"label.text.description*"}
                placeholder="Enter Description"
                variant={INPUT_STYLE_TYPE.SECONDARY}
                onChange={handleRejectLoanChange}
                value={rejectLoanAppDetails?.description}
                autoResize
              />
            </div>
          </div>
        </div>
      </DialogBox>
    );
  };

  return (
    <div className="flex flex-col  gap-5 ">
      <div className="flex items-center justify-between gap-5 w-full">
        <div className="flex items-center gap-5 w-11/12">
          <div className="border-r flex items-center border-secondary-stext-600 px-5 py-2 w-max ">
            <Text className="text-primary-ptext-1000 font-bold" label={"Stage & Status"} />
          </div>
          {/* Convert the below code to add scroll with arrows */}

          <div className="w-4/5">
            {/* Wrap your tabs with the ScrollableTabs component */}
            <ScrollableTabs />
          </div>
        </div>

        <div className="w-1/12">
          {!isLoanRejected ? (
            <ButtonBox
              className="bg-white"
              buttonType={BUTTON_TYPE.REJECT_APPLICATION}
              onClickWithLoader={onRejectApplication}
              size="LOAN_DATA"
            />
          ) : (
            <ButtonBox
              className="bg-white"
              buttonType={BUTTON_TYPE.RE_APPEAL}
              onClickWithLoader={onReAppealApplication}
              size="LOAN_DATA"
            />
          )}
        </div>
      </div>

      <div className="stage-status-layout-body-content  ">
        <Suspense fallback={<Loader />}>
          <Routes>
            {stageStatusLoginRoutes.map((route, index) => {
              return <Route key={index} path={route.path} element={<route.component />} />;
            })}
          </Routes>
        </Suspense>
        <Outlet />
      </div>

      {renderReAppealDialog()}

      {rejectLoanAppDetails?.isRejectAppConfirmationVisible ? renderRejectLoanDialog() : null}

      <ErrorDialog
        onHide={() => {
          setShowDialogAPIError(false);
          setErrorMessage("");
        }}
        visible={showDialogAPIError}
        errorMessage={errorMessage}
      />

      <ToastCustom
        position={position}
        ref={toastMessageRef}
        toastText={toastMessage}
        variant={toastMessageVariant}
      />
    </div>
  );
};

export default withRouter(StageAndStatus);
